// -------------- THEME SWITCHER -------------- //

@mixin theme($--bg-color, $--primary-text-color, $--secondary-text-color, $--link-color, $--visited-link-color) {
  background-color: $--bg-color;

  color: $--primary-text-color;

  a {
    color: $--link-color;
    text-decoration: none;

    //&:visited { color: $--visited-link-color }
    &:hover { text-decoration: underline; }
  }

  code:not(pre > code) {
    font-family: IBMVGA8, monospace;
    background-color: $--primary-text-color;
    color: $--bg-color;
  }

  figcaption { color: $--secondary-text-color; }
}

@mixin dark-appearance {
  @include theme(#000000, #6c6c6c, #eeeeee, #eeeeee, #eeeeee);
}

@mixin light-appearance {
  @include theme(#ffffff, #212121, #666666, #1e70bf, #921292);
}

body[a="dark"] { @include dark-appearance; }
body[a="light"] { @include light-appearance; }


@media (prefers-color-scheme: dark) {
  body[a="auto"] { @include dark-appearance; }
}

@media (prefers-color-scheme: light) {
  body[a="auto"] { @include light-appearance; }
}

// -------------------------------------------- //

@font-face {
  // https://int10h.org/oldschool-pc-fonts/
  font-family: "IBMVGA8";
  src: url('/fonts/ibm_vga8.woff2') format('truetype');
}


@keyframes blink {
  0% {opacity: 1;}
  40% {opacity: 1;}
  60% {opacity: 0;}
  100% {opacity: 0;}
}

.input-cursor { 
animation: blink .2s linear infinite alternate;
}

html { height: 100%; }

body {
  font-family: IBMVGA8, monospace;
  -webkit-font-smoothing: none!important;
  font-size: 18px;
  line-height: 1.4;
  margin: 0;
  min-height: 100%;
  overflow-wrap: break-word;
  text-align: left;
}

.post-meta { text-align: right; }

h1 { color: red;}

h2, h3, h4, h5, h6 { margin-top: 1rem; }

hr { margin: 2rem 0; }

p { margin: 1rem 0; }

strong {
  color: white;
}

li { margin: 0.4rem 0; }

*:target { background: #b00; }

.w {
  max-width: 900px;
  margin: 0 auto;
  padding: 4rem 2rem;
}

.toc {
  border: thin solid black;
  padding: 1rem;
}

hr {
  text-align: center;
  border: 0;

  &:before { content: '=======' }
  &:after { content: attr(data-content) '=======' }
}

pre {
  background: black;
  padding: 1em;
  overflow-x: auto; /* Fix pre content overflowing parent without scroll bar */
}

table { width: 100%; }

table, th, td {
  border: thin solid black;
  border-collapse: collapse;
  padding: 0.4rem;
}

code:not(pre > code) {
  padding: 0.1em 0.2em;
  font-size: 90%;
}

code.has-jax {
  -webkit-font-smoothing: antialiased;
  background: inherit !important;
  border: none !important;
  font-size: 100%;
}

blockquote {
  font-style: italic;
  border: thin solid black;
  padding: 1rem;

  p { margin: 0; }
}

img {
  max-width: 100%;
  display: block;
  margin: 0 auto;
}

img:hover {
  transform: scale(1.2);
}

figcaption {
  text-align: center;
  opacity: 0.5;
}
